import { UsersDataType } from "../../redux/store";

export const updateObjectInArray = (
    items: UsersDataType[],
    itemId: number,
    objPropName: keyof UsersDataType, //  keyof   UsersDataType
    newObjProps: Partial<UsersDataType> //  Partial   
) => {
    return items.map(u =>
        u[objPropName] === itemId ? { ...u, ...newObjProps } : u
    );
}
